@import url("https://fonts.googleapis.com/css2?family=Manrope&display=swap");

/* utilities */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: var(--font-family);
}

:root {
  --font-family: "Manrope", sans-serif;
  --gradient-text: linear-gradient(89.97deg, #ae67fa 1.84%, #f49);
  --gradient-bar: linear-gradient(103.22deg, #ae67fa -13.86%, #f49);
  --background: #052d56;
  --color-bg: #040618;
  --color-footer: #031b34;
  --color-blog: #042c54;
  --color-text: #81afdd;
  --color-subtext: #ff817a;
  --btn-bg: #ff4820;
  --text-color: #81afdd;
  --container: #1b78de;
}

body {
  /* ff 3.6+ */
  background: -moz-radial-gradient(
    circle at 3% 25%,
    rgba(0, 40, 83, 1) 0%,
    rgba(4, 12, 24, 1) 100%
  );

  /* safari 5.1+,chrome 10+ */
  background: -webkit-radial-gradient(
    circle at 3% 25%,
    rgba(0, 40, 83, 1) 0%,
    rgba(4, 12, 24, 1) 100%
  );

  /* opera 11.10+ */
  background: -o-radial-gradient(
    circle at 3% 25%,
    rgba(0, 40, 83, 1) 0%,
    rgba(4, 12, 24, 1) 100%
  );

  /* ie 10+ */
  background: -ms-radial-gradient(
    circle at 3% 25%,
    rgba(0, 40, 83, 1) 0%,
    rgba(4, 12, 24, 1) 100%
  );

  /* global 92%+ browsers support */
  background: radial-gradient(
    circle at 3% 25%,
    rgba(0, 40, 83, 1) 0%,
    rgba(4, 12, 24, 1) 100%
  );
}

.component-text {
  padding: 5rem;
}

/* header */
.background-clip {
  /* ff 3.6+ */
  background: -moz-linear-gradient(
    266deg,
    rgba(244, 152, 103, 1) 0%,
    rgba(174, 103, 250, 1) 100%
  );

  /* safari 5.1+,chrome 10+ */
  background: -webkit-linear-gradient(
    266deg,
    rgba(244, 152, 103, 1) 0%,
    rgba(174, 103, 250, 1) 100%
  );

  /* opera 11.10+ */
  background: -o-linear-gradient(
    266deg,
    rgba(244, 152, 103, 1) 0%,
    rgba(174, 103, 250, 1) 100%
  );

  /* ie 6-9 */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#F49867', endColorstr='#AE67FA', GradientType=0);

  /* ie 10+ */
  background: -ms-linear-gradient(
    266deg,
    rgba(244, 152, 103, 1) 0%,
    rgba(174, 103, 250, 1) 100%
  );

  /* global 94%+ browsers support */
  background: linear-gradient(
    266deg,
    rgba(244, 152, 103, 1) 0%,
    rgba(174, 103, 250, 1) 100%
  ) !important;
  background-clip: text !important;
  -webkit-background-clip: text !important;
  color: transparent;
}

.ls {
  letter-spacing: -0.04em;
}

.orange {
  color: #ff8a71 !important;
}

.skyblue {
  color: #71e5ff !important;
}

p {
  color: #81afdd !important;
}

.font-size-sm {
  font-size: 20px;
}

.lines {
  width: 38px;
  height: 3px;
  background: linear-gradient(
    266deg,
    rgba(244, 152, 103, 1) 0%,
    rgba(174, 103, 250, 1) 100%
  ) !important;
}

.line {
  width: 38px;
  height: 3px;
  background: linear-gradient(
    266deg,
    rgba(244, 152, 103, 1) 0%,
    rgba(174, 103, 250, 1) 100%
  ) !important;
  margin-bottom: 10px;
}

.btn-link {
  background-color: var(--btn-bg) !important;
  border-radius: 3px;
  padding: 9px 30px;
}

/* navigational */
.navbar {
  padding: 0 6rem;
}

/* header */
.header-form {
  width: 556px;
}

/* section */
.section-container {
  background: rgba(3, 3, 90, 0.651);
  width: 1170px !important;
  margin-top: 7rem;
}

.section-container p {
  color: #fff;
  opacity: 0.5;
}

.wgpt-text {
  padding-right: 7rem !important;
}

.cke-container h6 {
  font-size: 24px;
}

.possibilities-container {
  padding-top: 6rem;
  padding-bottom: 5rem;
}

.possibilities-container h4 {
  padding-right: 20rem;
}

/* futurebox */
.futurebox-container {
  margin-top: 55rem;
}

.future-container {
  margin-top: 5rem;
}

/* vr */
.vr-text {
  padding-top: 13rem;
}

/* register */
.register-container {
  background: linear-gradient(
    266deg,
    rgba(244, 152, 103, 1) 0%,
    rgba(174, 103, 250, 1) 100%
  ) !important;
  margin-bottom: 10px;
}

.register-text {
  color: black !important;
  font-weight: 800;
}

.register-link a {
  background-color: #000;
}

/* blog */
.blogging-container {
  width: 917.03px;
}

.blogging-container,
.blogging-container > p {
  color: #fff !important;
}

.blog-solo-text {
  background-color: var(--color-blog);
}

.blog-solo-text a {
  padding-top: 9.3rem;
}

.blog-solo-small-box {
  width: 280px !important;
  background-color: var(--color-blog);
}

/* footer */
footer{ 
background-color: #031B34;
}